@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;400;500;600;700;800&display=swap);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

html {
	min-width: 1140px;
}

.App {
	font-family: "Montserrat", sans-serif;
	background-color: #f1f1f1;
}

.navbar {
	background-color: #022f07;
	height: 80px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	font-size: 1.2rem;
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	z-index: 20;
  }
  
  .nav-container {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	width: 100%;
	height: 40px;
	max-width: 1500px;
  }
  
  .nav-logo {
	color: #ffdd40;
	-webkit-align-items: center;
	        align-items: center;
	margin-left: 20px;
	cursor: pointer;
	text-decoration: none;
	font-size: 2rem;
	-webkit-flex-grow: 1;
	        flex-grow: 1;
  }
  
  .nav-menu {
	display: -webkit-flex;
	display: flex;
	list-style: none;
	text-align: center;
	margin-right: 2rem;
  }
  
  .nav-links {
	color: #fff;
	text-decoration: none;
	padding: 0.5rem 1rem;
	height: 100%;
	border-bottom: 3px solid transparent;
  }
  .fa-code {
	margin-left: 1rem;
  }
  
  .nav-item {
	line-height: 40px;
	margin-right: 1rem;
  }
  
  .nav-item:after {
	content: "";
	display: block;
	height: 3px;
	width: 0;
	background: transparent;
	transition: width 0.7s ease, background-color 0.5s ease;
  }
  
  .nav-item:hover:after {
	width: 100%;
	background: #ffdd40;
  }
  
  .nav-item .active {
	color: #ffdd40;
	border: 1px solid #ffdd40;
  }
  
  .nav-icon {
	display: none;
  }
  
  @media screen and (max-width: 960px) {
	.nav-menu {
	  display: -webkit-flex;
	  display: flex;
	  -webkit-flex-direction: column;
	          flex-direction: column;
	  width: 100%;
	  border-top: 1pxsolid #fff;
	  position: absolute;
	  top: 80px;
	  left: -110%;
	  opacity: 1;
	  transition: all 0.5s ease;
	}
  
	.nav-menu.active {
	  background: #1f5156;
	  left: 0px;
	  opacity: 1;
	  transition: all 0.5s ease;
	  z-index: 1;
	}
	.nav-item .active {
	  color: #ffdd40;
	  border: none;
	}
	.nav-links {
	  padding: 1.5rem;
	  width: 100%;
	  display: table;
	}
  
	.nav-icon {
	  display: block;
	  position: absolute;
	  top: 0;
	  right: 0;
	  -webkit-transform: translate(-100%, 60%);
	          transform: translate(-100%, 60%);
	  font-size: 1.8rem;
	  cursor: pointer;
	  color: #ffdd40;
	}
  }
  
