.card {
	background: rgb(239, 243, 244);
	border: 0.5px solid rgb(0, 0, 0);
	width: 25em;
	border-radius: 0.5em;
	margin: 1em;
	overflow: hidden;
	cursor: pointer;
	box-shadow: 0 13px 27px -5px hsla(240, 30.1%, 28%, 0.25),
		0 8px 16px -8px hsla(0, 0%, 0%, 0.3), 0 -6px 16px -6px hsla(0, 0%, 0%, 0.03);
	transition: all ease 200ms;
}

.card:hover {
	-webkit-transform: scale(1.05);
	        transform: scale(1.05);
	box-shadow: 0 13px 40px -5px hsla(240, 30.1%, 28%, 0.12),
		0 8px 32px -8px hsla(0, 0%, 0%, 0.14),
		0 -6px 32px -6px hsla(0, 0%, 0%, 0.02);
}

.card img {
	padding: 3.5px;
	width: 100%;
	object-fit: cover;
	background-color: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
}

.card h2 {
	color: #222;
	margin-top: -0.2em;
	line-height: 1.4;
	font-size: 1.15em;
	font-weight: 500;
	font-family: "Montserrat", sans-serif;
	transition: all ease-in 100ms;
}

.card p {
	color: #777;
	font-size: 97%;
}

.card h5 {
	color: #bbb;
	font-weight: 700;
	font-size: 0.7em;
	letter-spacing: 0.04em;
	margin: 1.4em 0 0 0;
	text-transform: uppercase;
}

.card-body {
	padding: 1.2em;
}

.Circle {
	width: 50px;
	height: 50px;
	/* margin-left: 10px; */
	border-radius: 50%;
	background-color: #124416;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	z-index: 999;
	font-weight: 800;
	color: #ffdd40;

	box-shadow: 3px 4px 8px -1px rgba(0, 0, 0, 0.73);
	-webkit-animation: 1s ease 0s 1 normal none running crescendo;
	        animation: 1s ease 0s 1 normal none running crescendo;
}

@-webkit-keyframes crescendo {
	0% {
		-webkit-transform: scale(0);
		        transform: scale(0);
	}
	50% {
		-webkit-transform: scale(1.4);
		        transform: scale(1.4);
	}
	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}

@keyframes crescendo {
	0% {
		-webkit-transform: scale(0);
		        transform: scale(0);
	}
	50% {
		-webkit-transform: scale(1.4);
		        transform: scale(1.4);
	}
	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}

.Image {
	width: 35px;
	height: 35px;
	margin-left: 8px;
	margin-right: 8px;
	-webkit-animation-name: shakeX;
	        animation-name: shakeX;
	-webkit-animation-duration: 1s;
	        animation-duration: 1s;
}

@-webkit-keyframes shake {
	0% {
		-webkit-transform: scale(0) rotate(0deg);
	}
	10% {
		-webkit-transform: scale(1.1) rotate(-1deg);
	}
	20% {
		-webkit-transform: scale(1.25) rotate(-6deg);
	}
	30% {
		-webkit-transform: scale(1.5) rotate(-12deg);
	}
	40% {
		-webkit-transform: scale(1.75) rotate(-18deg);
	}
	50% {
		-webkit-transform: scale(2) rotate(-24deg);
	}
	60% {
		-webkit-transform: scale(1.75) rotate(18deg);
	}
	70% {
		-webkit-transform: scale(1.5) rotate(12deg);
	}
	80% {
		-webkit-transform: scale(1.25) rotate(6deg);
	}
	90% {
		-webkit-transform: scale(1.1) rotate(1deg);
	}
	100% {
		-webkit-transform: scale(0) rotate(0deg);
	}
}

@keyframes shake {
	0% {
		-webkit-transform: scale(0) rotate(0deg);
	}
	10% {
		-webkit-transform: scale(1.1) rotate(-1deg);
	}
	20% {
		-webkit-transform: scale(1.25) rotate(-6deg);
	}
	30% {
		-webkit-transform: scale(1.5) rotate(-12deg);
	}
	40% {
		-webkit-transform: scale(1.75) rotate(-18deg);
	}
	50% {
		-webkit-transform: scale(2) rotate(-24deg);
	}
	60% {
		-webkit-transform: scale(1.75) rotate(18deg);
	}
	70% {
		-webkit-transform: scale(1.5) rotate(12deg);
	}
	80% {
		-webkit-transform: scale(1.25) rotate(6deg);
	}
	90% {
		-webkit-transform: scale(1.1) rotate(1deg);
	}
	100% {
		-webkit-transform: scale(0) rotate(0deg);
	}
}

