.SingleNode_Animation {
	-webkit-animation-name: square;
	        animation-name: square;
	-webkit-animation-duration: 1s;
	        animation-duration: 1s;
}

@-webkit-keyframes square {
	0% {
		-webkit-transform: scale(0);
		        transform: scale(0);
	}
	20% {
		-webkit-transform: scale(1.1);
		        transform: scale(1.1);
		box-shadow: -1px 0px 6px 0px rgba(50, 50, 50, 0.87);
	}
	50% {
		-webkit-transform: scale(1.2);
		        transform: scale(1.2);
		box-shadow: -1px 0px 6px 0px rgba(50, 50, 50, 0.89);
	}
	70% {
		-webkit-transform: scale(1.12);
		        transform: scale(1.12);
		box-shadow: -1px 0px 6px 0px rgba(50, 50, 50, 0.9);
	}
	90% {
		-webkit-transform: scale(1.1);
		        transform: scale(1.1);
		box-shadow: -1px 0px 6px 0px rgba(50, 50, 50, 0.93);
	}
	100% {
		-webkit-transform: scale(0);
		        transform: scale(0);
	}
}

@keyframes square {
	0% {
		-webkit-transform: scale(0);
		        transform: scale(0);
	}
	20% {
		-webkit-transform: scale(1.1);
		        transform: scale(1.1);
		box-shadow: -1px 0px 6px 0px rgba(50, 50, 50, 0.87);
	}
	50% {
		-webkit-transform: scale(1.2);
		        transform: scale(1.2);
		box-shadow: -1px 0px 6px 0px rgba(50, 50, 50, 0.89);
	}
	70% {
		-webkit-transform: scale(1.12);
		        transform: scale(1.12);
		box-shadow: -1px 0px 6px 0px rgba(50, 50, 50, 0.9);
	}
	90% {
		-webkit-transform: scale(1.1);
		        transform: scale(1.1);
		box-shadow: -1px 0px 6px 0px rgba(50, 50, 50, 0.93);
	}
	100% {
		-webkit-transform: scale(0);
		        transform: scale(0);
	}
}

